.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
header {
  background: aliceblue;
  padding: 20px 0;
}

.menu {
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu-item {
  margin: 0 30px;
  list-style: none;
}

.menu-item a {
  font-size: 20px;
  line-height: normal;
  text-decoration: none;
  text-transform: uppercase;
  color: red;
  font-weight: 400;
}
